.splash__cont {
  background-color: var(--black);
}
.splash__list ul {
  width: 100%;
  columns: 4;
  column-gap: 20px;
}
.splash__list ul img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  margin-bottom: 20px;
  border-radius: 5px;
  cursor: pointer;
  background: #f9f9f9;
}
.splash__search {
  background: var(--black);
  .container {
    position: relative;
  }
  h2 {
    color: var(--white);
    font-size: 40px;
    height: 0;
    text-indent: -9999px;
    width: 0;
  }
  input {
    background: var(--black);
    border: 2px solid var(--light_border);
    border-radius: 50px;
    color: #f0eeeb;
    color: var(--light_bg);
    margin: 0 1% 5%;
    padding: 1rem 3rem 1rem 2rem;
    width: 98%;
  }
  button {
    background: var(--white);
    border: 0;
    border-radius: 50%;
    color: var(--black);
    font-family: var(--subKor_font);
    font-size: 12px;
    height: 40px;
    position: absolute;
    right: 22px;
    top: 9px;
    transition: opacity 0.3s ease;
    width: 40px;
  }
}

.random-slide {
  height: auto;
  padding-bottom: 10vw;
  background: #000;
  height: 50vw;

  .title h1 {
    color: white;
    font-family: "Saol";
    font-size: 50px;
    margin-left: 4.5vw;
  }

  .swiper {
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
  }

  .swiper-slide {
    text-align: center;
    font-size: 18px;

    /* Center slide text vertically */
    display: -webkit-box;
    display: -ms-flexbox;
    display: -webkit-flex;
    display: flex;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    -webkit-justify-content: center;
    justify-content: center;
    -webkit-box-align: center;
    -ms-flex-align: center;
    -webkit-align-items: center;
    align-items: center;
  }

  .swiper-slide img {
    display: block;
    width: 100%;
    height: 100%;
    object-fit: cover;
  }

  .random-item {
    display: flex;
    align-items: center;
    justify-content: center;
    max-height: 35vh;

    img {
      width: 70%;
      height: 100%;
    }
  }
}
